<template>
<div class="list">
    <div class="btn_box">
      <mt-button type="primary" size="small" @click="editAddress">新增</mt-button>
    </div>
   <ul
  v-infinite-scroll="loadMore"
  infinite-scroll-disabled="loading"
  infinite-scroll-distance="50"
  >
    <li v-for="(item, i) in list" :key="i" @click="toPage">
      <div class="flex_item">
        <div>
          <div class="flex_box">
            <span>工区编号：</span>
            <div class="flex_items">{{item.a}}</div>
          </div>
          <div class="flex_box">
            <span>工区名称：</span>
            <div class="flex_items">{{item.b}}</div>
          </div>
          <div class="flex_box">
            <span>签到间隔：</span>
            <div class="flex_items">{{item.b}}</div>
          </div>
        </div>
      </div>
      <div class="btn_box">
        <div>
          <mt-button type="primary" size="small" @click="editAddress">编辑</mt-button>
        </div>
        <div>
          <mt-button type="default" size="small" @click="delAddress">删除</mt-button>
        </div>
      </div>
    </li>
  </ul>
</div>
</template>
<script>
export default {
  data () {
    return {
      list: [
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        }
      ]
    }
  },
  methods: {
    // 编辑工区地址
    editAddress (type) {
      this.$router.push({name: 'EditAddress'})
    },
    // 下拉加载
    loadMore () {
      this.loading = false
      let a = [
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        },
        {
          a: '地点A',
          b: '2018-03-05  15:20:00'
        }
      ]
      this.list = [...this.list, ...a]
    },
    toPage () {
      this.$router.push({name: 'areaDet'})
    }
  }
}
</script>
<style lang="scss" scoped>
.list {
  height: 100%;
  > .btn_box {
    padding: 15px;
  }
  > div:not(.btn_box) {
    box-sizing: border-box;
  }
  ul {
    height: calc(100% - 70px);
    overflow: auto;
    border-top: 1px solid #eee;
  }
  li {
    display: flex;
    padding: 15px;
    align-items: center;
    border-bottom: 1px solid #eee;
    .btn_box {
      width: 70px;
      > div {
        float: right;
        &:first-child {
          margin-bottom: 10px;
        }
      }
    }
    li:last-child {
      border-bottom: none;
    }
  }
}
.flex_item {
  flex: 1;
  width: calc(100% - 70px);
  div {
    > div {
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
.flex_box {
  display: flex;
  span {
    font-weight: bold;
    width: 80px;
  }
}
.flex_items {
  flex: 1;
  width: calc(100% - 80px);
}
</style>



